<script>
import BaseButton from "@/components/common/Base.Button.vue";
import BaseInput from "@/components/common/Base.Input.vue";
import BaseModal from "@/components/common/Base.Modal.vue";

export default {
  name: "Certificate",
  components: {
    BaseButton,
    BaseInput,
    BaseModal,
  },
  data() {
    return {
      active: 1,
      certificateNumber: "",
      certificateNumberSuggestion: false,
      email: "",
      certificateArticleSuggestion: false,
      certificateArticle: "",
      openModal: false,
      certificates: [
        {
          id: 0,
          article: "EP.01",
          name: "Помпа ирригационная эндоскопическая ENDOMATE PUMP",
        },
        {
          id: 1,
          article: "EP.02",
          name: "Помпа ирригационная эндоскопическая ENDOMATE PUMP",
        },
      ],
      suggestions: ["8/21-ES", "7/21-ES", "6/21-ES", "5/21-ES"],
    };
  },
  methods: {
    send() {
      console.log("send");
      this.openModal = true;
    },
    download() {
      console.log("download");
    },
    addArticle() {
      const id =
        this.certificates.length &&
        this.certificates[this.certificates.length - 1].id;
      const obj = {
        id: id ? id + 0 : 0,
        article: "adsads",
        name: "asdasdasdasdasdasdasdgsgsdkfgluodsf;hysglf;hdvnkbhskzluhf;dilk.",
      };
      this.certificates.push(obj);
    },
    removeProduct(id) {
      const row = this.certificates.findIndex((i) => i.id == id);
      this.certificates.splice(row, 1);
    },
  },
};
</script>

